<template>
  <v-container
  >
    <v-row
        no-gutters
    >
      <v-card
          class="white pa-2 mx-0"
          tile
      >
        <v-card-title>Konten:</v-card-title>
        <v-card-text>
          <v-row >
            <v-col cols="3">
              <v-select
                  v-model="sKlassen"
                  :items="mKlassen"
                  label="Klasse wählen"
                  @change="classSelected"
                  align="top"
              ></v-select>
            </v-col>
            <v-col cols="3" >
              <v-text-field
                  v-model="passwort"
                  label="Neues Passwort"
              ></v-text-field>
            </v-col>
            <v-col>
            </v-col>

          </v-row>
          <v-alert
              :value="alert"
              :type="alertColor"
              dark
              icon="mdi-account"
              transition="slide-y-transition"
          >
            {{alertText}}
          </v-alert>
            <v-data-table
                :headers="headers"
                :items="konten"
                :items-per-page="dataSize"
                item-key="loginName"
                class="table-striped elevation-1"
                :hide-default-footer="true"
            >
              <template v-slot:item.anzeigeName="{ item }">
                <v-text-field v-model="editedItem.anzeigeName" :hide-details="true" dense single-line v-if="item.loginName === editedItem.loginName"></v-text-field>
                <span v-else>{{item.anzeigeName}}</span>
              </template>
              <template v-slot:item.loginName="{ item }">{{item.loginName}}</template>
              <template v-slot:item.nachname="{ item }">
                <v-text-field v-model="editedItem.nachname" :hide-details="true" dense single-line v-if="item.loginName === editedItem.loginName"></v-text-field>
                <span v-else>{{item.nachname}}</span>
              </template>
              <template v-slot:item.vorname="{ item }">
                <v-text-field v-model="editedItem.vorname" :hide-details="true" dense single-line v-if="item.loginName === editedItem.loginName"></v-text-field>
                <span v-else>{{item.vorname}}</span>
              </template>
              <template v-slot:item.email="{ item }">
                <v-text-field v-model="editedItem.email" :hide-details="true" dense single-line v-if="item.loginName === editedItem.loginName"></v-text-field>
                <span v-else>{{item.email}}</span>
              </template>
              <template v-slot:item.actions="{ item }">
                <div v-if="item.loginName === editedItem.loginName">
                  <v-icon color="red" class="mr-2" @click="close">
                    mdi-window-close
                  </v-icon>
                  <v-icon color="green"  @click="save">
                    mdi-content-save
                  </v-icon>
                </div>
                <div v-else>

                  <v-icon color="green" class="mr-2" @click="editItem(item)">
                    mdi-pencil
                  </v-icon>

                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-icon
                          color="red"
                          @click="newPass(item)"
                          v-bind="attrs"
                          v-on="on"                      >
                        mdi-lock-alert
                      </v-icon>
                    </template>
                    <span>Passwort setzen</span>
                  </v-tooltip>
                </div>
              </template>

            </v-data-table>
        </v-card-text>
      </v-card>
    </v-row>



  </v-container>
</template>

<script>
import TransferService from '../services/transfer.service';
import {mapGetters} from "vuex";
import * as Const from '../config';

//import moment from "moment";


export default {
  name: 'Home',

  data () {
    return {
      sKlassen: [],
      konten: [],
      passwort: 'bkest202122',
      login:'',
      alert: false,
      alertColor:"success",
      alertText: '',
      editedIndex: -1,
      editedItem: {
        anzeigeName: '',
        loginName: '-1',
        nachname: '',
        vorname: '',
        email: '',
      },

      dataSize: 0,

      headers: [
        {
          text: 'Klasse',
          align: 'start',
          sortable: true,
          value: 'klasse',
        },
        {text: 'Name', value: 'anzeigeName'},
        {text: 'Login', value: 'loginName'},
        {text: 'Nachname', value: 'nachname'},
        {text: 'Vorname', value: 'vorname'},
        {text: 'EMail', value: 'email'},
        { text: 'Actions', value: 'actions', sortable: false },
      ],

      mKlassen: [],
    }
  },

  computed: {
    ...mapGetters( 'auth', ['currentUser']),

  },

  mounted() {
    if (!this.currentUser) {
      this.$router.push('/login');
    } else {
      this.loadConfig();

      TransferService.get(Const.READ_EXT_KLASSEN).then((data) => {
//      this.$store.dispatch('domain/loadEplan', {path: path}).then((data) => {
        if (data != undefined && data.length > 0) {
          this.mKlassen = data;
          this.dataSize = data.length;
        }
      });
    }
  },

  methods: {
    loadConfig() {
      this.alert=false;
    },

    editItem (item) {
      this.editedIndex = this.konten.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },

    close () {
      setTimeout(() => {
//        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedItem.loginName = '';
        this.editedIndex = -1;
      }, 300)
    },

    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.konten[this.editedIndex], this.editedItem)
        let that = this;
        that.login =  this.editedItem.loginName;
        TransferService.post(Const.UPDATE_EXT_ACCOUNT,  this.editedItem).then((res) => {
          this.handleAlert(res, that, "Kontodaten geschrieben für " + that.login + ".");
        });
      }
      this.close();
    },

    newPass(item){
      let that = this;
      that.login = item.loginName;
      TransferService.post(Const.WRITE_PW,  {id:that.login, pw:this.passwort}).then((res) => {
        this.handleAlert(res, that, 'Passwort für ' + that.login + ' gesetzt');
      });
    },

    handleAlert(res, that, text){
      console.log(text);
      that.alert = true;
      that.alertColor = res ? "success" : "error";
      that.alertText = text;
      window.setTimeout(() => {
        that.alert = false;
        that.alertText = '';
      }, 4000)
    },

    classSelected(item){
      console.log(item)

      let that = this;
      this.passwort = 'bkest202122' + item;
      TransferService.post(Const.READ_EXT_ACCOUNTS,  item).then((res) => {
//      this.$store.dispatch('domain/loadEplan', {path: path}).then((data) => {
        let data = res.data;
        if(data !== undefined && data.length > 0){
          that.konten = data;
        }
      });
    }
  }
}

</script>

<style lang='scss'>
//$textSize:0.9rem;
//@import "~vue-tabulator/dist/scss/bootstrap/tabulator_bootstrap4";
//@import "~vue-tabulator/dist/scss/tabulator";
@import "~tabulator-tables/dist/css/tabulator";

tbody tr:nth-of-type(even) {
  background-color: rgba(236, 237, 237);
}

tbody tr:nth-of-type(odd) {
  background-color: rgb(250 ,250, 250);
}

.v-data-table-header {
  background-color: rgb(240, 240, 240);
  color: white;
}

.v-data-footer {
  background-color: rgb(250 ,250, 250);
}

.theme--light.v-data-table thead tr th {
  color: white;
}
</style>