<template>
  <v-app>
    <v-app-bar
      app
      color="primary"
      dark
    >
      <v-app-bar-nav-icon v-if="isTeacher" to="/"></v-app-bar-nav-icon>

      <v-btn v-if="isAccManager" icon to="/loaddata">
        <v-icon>mdi-upload</v-icon>
      </v-btn>

      <v-btn v-if="isAccManager" icon to="/checkdata">
        <v-icon>mdi-compare-horizontal</v-icon>
      </v-btn>

      <v-spacer></v-spacer>

      <v-toolbar-title>BKEST-Accounts</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn v-if="isAdmin" icon to="/config">
        <v-icon>mdi-cog</v-icon>
      </v-btn>

      <!--      <v-btn to="/login"-->
<!--             text-->
<!--             v-if="!currentUser"-->
<!--      >-->
<!--        <span class="mr-2">Login</span>-->
<!--        <v-icon>mdi-login</v-icon>-->
<!--      </v-btn>-->
<!--      <v-btn to="/profile"-->
<!--             text-->
<!--             v-if="currentUser"-->
<!--      >-->
      <!--        <span class="mr-2">{{ currentUser.username }}</span>-->
<!--        <v-icon>mdi-account</v-icon>-->
<!--      </v-btn>-->
      <v-btn href @click.prevent="logOut"
             text
      >
        <span v-if="currentUser" class="mr-2">{{ currentUser.username }} </span>
        <span class="mr-2">LogOut</span>
        <v-icon>mdi-logout</v-icon>
      </v-btn>

    </v-app-bar>

    <v-main>
      <router-view />
    </v-main>
    <v-footer app height="45">
      <v-row no-gutters>
        <v-col cols="1" align="left">
          {{time}}</v-col>
        <v-col cols="1" align="left">
          ({{lastUpdate}})</v-col>
        <v-col cols="8" align="left">
          <span  v-if="stale === 'false'">{{lastMessage}}</span>
        </v-col>
        <v-col cols="2" align="right">
          <v-icon>mdi-copyright</v-icon>
          J. Hergeth
        </v-col>
      </v-row>
    </v-footer>
    <v-footer v-if="toDo > 0" app height="5">
      <v-row no-gutters>
        <v-col cols="12">
          <v-progress-linear
              v-model="progvalue"
              :buffer-value="progvalue"
          >
          </v-progress-linear>
        </v-col>
      </v-row>
      </v-footer>
  </v-app>
</template>

<script>
import {mapGetters} from 'vuex';
// import AuthService from './services/auth.service';
import TransferService from "@/services/transfer.service";
import * as Const from './config';


export default {
  name: 'App',

  data: () => ({
    time: '',
    lastUpdate: '0:00',
    lastMessage:'',
    toDo: 0,
    done: 0,
    stale: false,
    progvalue:0,
  }),

  mounted(){
//    AuthService.create(this);

    setInterval(() => {
      TransferService.get(Const.STATUS_API).then((data) => {
//      this.$store.dispatch('domain/loadEplan', {path: path}).then((data) => {
        let currentDate = new Date();
        let time = currentDate.getHours() + ":" + currentDate.getMinutes() + ":" + currentDate.getSeconds();
        this.time = time;
        if(data != undefined){
          this.lastUpdate = data.timeSet;
          this.lastMessage = data.message;
          this.toDo = data.todo;
          this.done = data.done;
          this.stale = data.stale;

          if(data.todo > 0){
            this.progvalue = data.done/data.todo*100;
          }
          else{
            this.progvalue = 0;
            this.toDo = 0;
          }
          if(this.stale !== 'false'){
            this.toDo = 0;
          }
        }
      })
    }, 500);
  },
  computed: {
    ...mapGetters( 'auth', ['currentUser', 'isAdmin', 'isTeacher', 'isAccManager' ]),
  },
  methods: {
    logOut() {
      this.$store.dispatch('auth/logout');
      this.$router.push('/login');
    },
  }
};
</script>
